export const Importing = `import { Chaperone, ChaperoneStep, Trigger } from "chaperone";`;

export const GettingStarted = `import React from "react";
import { Chaperone, ChaperoneStep, Trigger } from "chaperone";

#render(
    <div className="container">  
        <Chaperone>
            <Trigger label="Need Help?" />
            <form className="mt-3">
                <div className="n-form-field">
                    <label htmlFor="gs-email">Email address</label>
                    <ChaperoneStep 
                        index={0} 
                        message="To Login. Firstly, you must type your e-mail here." 
                        placement="top-right">
                        <input 
                            type="email" 
                            className="n-form-control" 
                            id="gs-email" 
                            aria-describedby="emailHelp" 
                            style={{backgroundColor: "transparent"}} />
                    </ChaperoneStep>
                </div>
                <div className="n-form-field">
                    <label htmlFor="gs-pass">Password</label>
                    <ChaperoneStep 
                        index={1} 
                        message="Secondly, you must put your password here." 
                        placement="top-right">
                        <input 
                            type="password" 
                            className="n-form-control" 
                            id="gs-pass" 
                            style={{backgroundColor: "transparent"}} />
                    </ChaperoneStep> 
                </div> 
                <div className="n-form-field"> 
                    <label className="n-form-check">
                        <ChaperoneStep 
                            index={2} 
                            message={<>Thirdly, if you want to remember the app your inputs just click <b>remember me</b>.</>} 
                            placement="left">
                            <input className="n-form-control" type="checkbox" /> 
                        </ChaperoneStep> 
                        remember me
                    </label> 
                </div>
                <ChaperoneStep 
                    isDone 
                    index={3} 
                    message={<>Lastly, you must press <b>Submit</b>.</>}>
                    <button type="button" className="n-btn">Submit</button>
                </ChaperoneStep> 
            </form>
        </Chaperone>
    </div>
)`;

export const ChaperoneStepSingleton = `import React from "react";
import { Chaperone, ChaperoneStep, Trigger } from "chaperone";
const chaperoneData = [
    {
        index: 0,
        message: <b>Hello there!!</b>,
        find: "img"
    },
    {
        index: 1,
        message: "I'am Fernando.",
        placement: "left",
        find: "#p-name"
    },
    {
        index: 2,
        message: <>aka <b>Mytabworks</b></>,
        placement: "bottom-right",
        find: (children) => children.querySelector("#p-aka")
    },
    {
        index: 3,
        message: "It can even find outside the children scope.",
        placement: "bottom",
        isDone: true,
        find: () => document.querySelector("#chaperonestep-singleton > div")
    }
]
#render(
    <div className="container mb-4">  
        <Chaperone>
            <Trigger label="click here to know me." allowTransition />
            <ChaperoneStep
                navigation={chaperoneData.length}
                placement="top-right"
                propertyMutation={({ index, currentId }, defaultProps) => {
                    return {
                        ...defaultProps, 
                        ...chaperoneData.find(props => props.index === index)
                    }
                }}>
                <div className="d-flex flex-center-y mt-4">
                    <img className="b-round mr-4" src="/images/icons/icon-128x128.png" alt="beard-man"/>
                    <span id="p-name" className="mr-1">Fernando</span>
                    <span id="p-aka">(Mytabworks)</span>
                </div>
            </ChaperoneStep>
        </Chaperone>
    </div>
)`;

export const ChaperoneMultipleID = `import React from "react";
import { Chaperone, ChaperoneStep, Trigger } from "chaperone";

#render(
    <div className="container">  
        <Chaperone onDone={({previousId, trigger}) => {
            if(previousId === "guide-email") {
                trigger("guide-password")
            }
        }}>
            <Trigger id="guide-email" label="Email Help?" allowTransition /><br/>
            <Trigger id="guide-password" label="Password Help?" allowTransition /><br/>
            <form className="mt-3">
                <div className="n-form-field">
                    <ChaperoneStep 
                        navigation={2}
                        id="guide-email" 
                        index={0} 
                        message="It is the Email address label." 
                        placement="top-right"
                        allowSkip={false}>
                        <label htmlFor="mi-email">Email address</label>
                    </ChaperoneStep>
                    <ChaperoneStep 
                        isDone
                        navigation={2}
                        id="guide-email" 
                        index={1} 
                        message="you must type your valid e-mail here." 
                        placement="right-top"
                        onDone={({target, children, dialog}) => { 
                            const test = /^\\w+@\\w+\\.[a-z]{2,4}$/.test(children.value)
                            children.style.borderColor = test ? null : 'red'
                            return test
                        }}>
                        <input 
                            type="email" 
                            className="n-form-control" 
                            id="mi-email" 
                            aria-describedby="emailHelp" 
                            style={{backgroundColor: "transparent"}} />
                    </ChaperoneStep>
                </div>
                <div className="n-form-field">
                    <ChaperoneStep 
                        id="guide-password" 
                        index={0} 
                        message="It is the password label." 
                        placement="top-right"
                        allowSkip={false}>
                        <label htmlFor="mi-pass">Password</label>
                    </ChaperoneStep>
                    <ChaperoneStep 
                        isDone
                        id="guide-password" 
                        index={1} 
                        message="you must put your password here." 
                        placement="top-right">
                        <input 
                            type="password" 
                            className="n-form-control" 
                            id="mi-pass" 
                            style={{backgroundColor: "transparent"}} />
                    </ChaperoneStep> 
                </div>  
                <div className="n-form-field"> 
                    <label className="n-form-check">
                        <input className="n-form-control" type="checkbox" /> 
                        remember me
                    </label> 
                </div>
                <button type="button" className="n-btn">Submit</button>
            </form>
        </Chaperone>
    </div>
)`;

export const ChaperoneWithGestures = `import React from "react";
import { Chaperone, ChaperoneStep, Trigger } from "chaperone";

#render(
    <div className="container">  
        <Chaperone>
            <Trigger label="Need Help?" allowTransition /><br/> 
            <form>
                <div className="row">
                    <div className="n-form-field column-md-6">
                        <label htmlFor="inputEmail4">Email</label>
                        <ChaperoneStep
                            allowNext={false}
                            allowBack={false} 
                            propertyMutation={({ currentId, index }, defaultProps) => {
                                const props = [
                                    {
                                        index: 0, 
                                        message: "click the email input box.",
                                        placement: "top-right",
                                        allowNextOnClick: true,
                                        gesture: (
                                            <div className="custom-click-gesture">
                                                <i className="mytabworks hand-pointer"/>
                                            </div>
                                        )
                                    },
                                    {
                                        index: 1, 
                                        message: "input a valid e-mail address.",
                                        placement: "right",
                                        allowNextOnType: true,
                                        onNext: ({target, children, dialog}) => { 
                                            const test = /^\\w+@\\w+\\.[a-z]{2,4}$/.test(children.value)
                                            children.style.borderColor = test ? null : 'red'
                                            return test
                                        },
                                        gesture: (
                                            <div className="custom-typing-gesture">
                                                <i className="mytabworks icon-keyboard"/>
                                            </div>
                                        )
                                    },
                                ]
                                return { 
                                    ...defaultProps,
                                    ...props.find((each) => each.index === index)
                                }
                            }}>
                            <input type="email" className="n-form-control" id="inputEmail4" placeholder="Email"/>
                        </ChaperoneStep>
                    </div>
                    <div className="n-form-field column-md-6">
                        <label htmlFor="inputPassword4">Password</label>
                        <ChaperoneStep 
                            allowNext={false}
                            allowBack={false} 
                            propertyMutation={({ currentId, index }, defaultProps) => {
                                const props = [
                                    {
                                        index: 2, 
                                        message: "click the password input box.",
                                        placement: "top-right", 
                                        allowNextOnClick: true,
                                        gesture: (
                                            <div className="custom-click-gesture">
                                                <i className="mytabworks hand-pointer"/>
                                            </div>
                                        )
                                    },
                                    {
                                        index: 3, 
                                        message: "input your password.",
                                        placement: "right",
                                        allowNextOnType: true,
                                        onNext: ({target, children, dialog}) => {  
                                            const test = target.value.length > 7
                                            children.style.borderColor = test ? null : 'red'
                                            return test
                                        },
                                        gesture: (
                                            <div className="custom-typing-gesture">
                                                <i className="mytabworks icon-keyboard"/>
                                            </div>
                                        )
                                    },
                                ]
                                return { 
                                    ...defaultProps,
                                    ...props.find((each) => each.index === index)
                                }
                            }}>
                            <input type="password" className="n-form-control" id="inputPassword4" placeholder="Password" />
                        </ChaperoneStep>
                    </div>
                </div>
                <div className="n-form-field">
                    <label htmlFor="inputAddress">Address</label>
                    <input type="text" className="n-form-control" id="inputAddress" placeholder="1234 Main St" />
                </div>
                <div className="n-form-field">
                    <label htmlFor="inputAddress2">Address 2</label>
                    <input type="text" className="n-form-control" id="inputAddress2" placeholder="Apartment, studio, or floor" />
                </div>
                <div className="row">
                    <div className="n-form-field column-md-6">
                        <label htmlFor="inputCity">City</label>
                        <input type="text" className="n-form-control" id="inputCity" />
                    </div>
                    <div className="n-form-field column-md-4">
                        <label htmlFor="inputState">State</label>
                        <select id="inputState" className="n-form-control">
                        <option>Choose...</option>
                        <option>...</option>
                        </select>
                    </div>
                    <div className="n-form-field column-md-2">
                        <label htmlFor="inputZip">Zip</label>
                        <input type="text" className="n-form-control" id="inputZip" />
                    </div>
                </div>
                <div className="n-form-field"> 
                    <label className="n-form-check">
                        <input className="n-form-control" type="checkbox" /> 
                        Check me out
                    </label> 
                </div>
                <ChaperoneStep 
                    isDoneOnClick
                    index={4}
                    message="submit your input."
                    gesture={(
                        <div className="custom-click-gesture">
                            <i className="mytabworks hand-pointer"/>
                        </div>
                    )}>
                    <button type="button" className="n-btn">Sign in</button>
                </ChaperoneStep>
            </form>
        </Chaperone>
    </div>
)`; 