import React from 'react';
import PropTypes from 'prop-types';

export const ChaperoneButton = ({ label, onClick, ...props }) => {
	return (
		<button
			type='button'
			{...props}
			className='chaperone-btn'
			onClick={onClick}>
			{label}
		</button>
	);
};

ChaperoneButton.propTypes = {
	label: PropTypes.string,
	onClick: PropTypes.func
};
