import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';
import * as Table from './Tables';

export const Dropdown = () => {
	const { getSession, setSession } = useSession();
	const title = 'Dropdown';
	const context = `it is a toggable contextual overlays for displaying lists of links.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<DropdownMain />}
				side={<DropdownSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Dropdown.propTypes = {};

const DropdownMain = () => (
	<>
		<Section.Example /> 
		<Section.LiveDemo />
		<Section.Dropup />
		<Section.Dropend />
		<Section.Interactive />
		<Section.DropdownTree />
		<Section.Javascript />
		<Table.Methods />
		<Table.Events />
		<Section.Events />
	</>
);

const DropdownSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Dropdown Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.dropdown}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius']}
			allowFloat
		/>
	</ul>
);
