import React from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
export const FlatStyle = () => (
	<Section
		title='Flat shadow'
		context={<>by putting <b>.n-flat</b> class on any element, it will transform the element into a flat on top. adding suffix <b>-low</b> in class will display low bulge.</>}>
		<HoverCopy code={code.FlatStyle}>
			<CodeReadOnly code={code.FlatStyle} />
		</HoverCopy>
		<div className="n-flat-low circle-eg"></div><br/>
		<div className="n-flat-low rectangle-eg"></div><br/>
		<div className="n-flat circle-eg"></div><br/>
		<div className="n-flat rectangle-eg"></div>
	</Section>
);

