export const HoverStyle = () => (
	<Section
		title='Hover shadow'
		context={<>by putting word <b>hover</b> before the name of shadow e.g. <b>.n-hover-flat</b>, it will transition into that shadow.</>}>
		<HoverCopy code={code.HoverStyle}>
			<CodeReadOnly code={code.HoverStyle} />
		</HoverCopy>
		<div className="n-flat-low n-hover-engrave-low circle-eg"></div>
		<div className="n-engrave n-hover-flat circle-eg"></div>   
		<div className="n-float-low n-hover-convex-low circle-eg"></div>
		<div className="n-convex n-hover-float circle-eg"></div>
		<div className="n-concave-low n-hover-no-shadow circle-eg"></div>
		<div className="n-engrave-low n-hover-concave-low circle-eg"></div>
	</Section>
);
export const NoShadow = () => (
	<Section
		title='No shadow'
		context='it is use to display no shadow without losing animation.'>
		<HoverCopy code={code.NoShadow}>
			<CodeReadOnly code={code.NoShadow} />
		</HoverCopy>
		<div className="n-no-shadow n-hover-flat-low circle-eg">
			<i className="mytabworks icon-anchor"/>
		</div>
		<div className="n-no-shadow n-hover-engrave-low circle-eg">
			<i className="mytabworks icon-ship"/>
		</div>
		<div className="n-no-shadow n-hover-concave-low circle-eg">
			<i className="mytabworks icon-globe"/>
		</div>
		<div className="n-no-shadow n-hover-convex-low circle-eg">
			<i className="mytabworks icon-bulb"/>
		</div>
		<div className="n-no-shadow n-hover-float-low circle-eg">
			<i className="mytabworks icon-paper-plane"/>
		</div>
		<br/><br/>
		<div className="n-no-shadow n-hover-flat circle-eg">
			<i className="mytabworks icon-anchor"/>
		</div>
		<div className="n-no-shadow n-hover-engrave circle-eg">
			<i className="mytabworks icon-ship"/>
		</div>
		<div className="n-no-shadow n-hover-concave circle-eg">
			<i className="mytabworks icon-globe"/>
		</div>
		<div className="n-no-shadow n-hover-convex circle-eg">
			<i className="mytabworks icon-bulb"/>
		</div>
		<div className="n-no-shadow n-hover-float circle-eg">
			<i className="mytabworks icon-paper-plane"/>
		</div>
	</Section>
);