import React from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
export const Example = () => (
	<Section
		title='Examples'
		context='by adding .n-btn has class you can achieve neumorphic design button.'>
		<>
			<HoverCopy code={code.Example}>
				<CodeReadOnly code={code.Example} />
			</HoverCopy>
			<button className='n-btn' type='button'>
				Button
			</button>
			&nbsp;&nbsp;
			<button className='n-btn' type='button'>
				<i className='mytabworks icon-ship' /> button
			</button>
			&nbsp;&nbsp;
			<button className='n-btn' type='button'>
				<i className='mytabworks icon-anchor' />
			</button>
		</>
	</Section>
);

export const ButtonTags = () => (
	<Section
		title='Button Tags'
		context='The .n-btn classes are designed to be used with the <button> element. However, you can also use these classes on <a> or <input> elements'>
		<>
			<HoverCopy code={code.ButtonTags}>
				<CodeReadOnly code={code.ButtonTags} />
			</HoverCopy>
			<button className='n-btn' type='button'>
				Button
			</button>
			&nbsp;&nbsp;
			<a className='n-btn' href='#' role='button'>
				Link
			</a>
			&nbsp;&nbsp;
			<input className='n-btn' type='button' value='Input' />
			&nbsp;&nbsp;
			<input className='n-btn' type='submit' value='Submit' />
			&nbsp;&nbsp;
			<input className='n-btn' type='reset' value='Reset' />
		</>
	</Section>
);

export const ButtonSize = () => (
	<Section
		title='Button Sizes'
		context='add .n-btn-sm class to make the button small and add .n-btn-lg class to make the button large'>
		<>
			<HoverCopy code={code.ButtonSize}>
				<CodeReadOnly code={code.ButtonSize} />
			</HoverCopy>
			<button className='n-btn n-btn-xs' type='button'>
				xs
			</button>
			&nbsp;&nbsp;
			<button className='n-btn n-btn-sm' type='button'>
				small
			</button>
			&nbsp;&nbsp;
			<button className='n-btn n-btn-lg' type='button'>
				Large
			</button>
		</>
	</Section>
);

export const ActiveState = () => (
	<Section title='Active state' context='Buttons will appear pressed'>
		<>
			<HoverCopy code={code.ActiveState}>
				<CodeReadOnly code={code.ActiveState} />
			</HoverCopy>
			<button className='n-btn active' type='button'>
				Button
			</button>
			&nbsp;&nbsp;
			<button className='n-btn active' type='button'>
				<i className='mytabworks icon-ship' /> button
			</button>
		</>
	</Section>
);

export const DisabledState = () => (
	<Section
		title='Disabled state'
		context='Make buttons look inactive by adding the disabled'>
		<>
			<HoverCopy code={code.DisabledState}>
				<CodeReadOnly code={code.DisabledState} />
			</HoverCopy>
			<button className='n-btn' type='button' disabled>
				Button
			</button>
			&nbsp;&nbsp;
			<button className='n-btn' type='button' disabled>
				<i className='mytabworks icon-ship' /> button
			</button>
		</>
	</Section>
);
