import React from 'react';
import { Section, Table } from 'Templates';

const ExampleRows = [
    {
		name: 'destroy',
		type: 'string',
		default: '-',
		description: 'it is use to destroy and kill the Example.',
	},
]

export const Example = () => (
	<Section
		title='Example'>
		<Table.ForAPI data={ExampleRows} />
	</Section>
);