import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, SidePanel, HeadGear } from 'Templates';
import * as Section from './Sections';

export const Utilities = () => {
	const { getSession, setSession } = useSession();
	const title = 'Utilities';
	const context = `it is a few utilities that has to do with the theme colors.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<UtilitiesMain />}
				side={<UtilitiesSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Utilities.propTypes = {};

const UtilitiesMain = () => (
	<>
		<Section.Background /> 
        <Section.BackgroundIntense />
        <Section.TextColor />
        <Section.TextDimColor />
        <Section.BorderColor />
        <Section.Bordered /> 
	</>
);
const list = [
    { label: 'Background theme color' },
    { label: 'Background theme intensify color' },
    { label: 'Text theme color' },
    { label: 'Text theme dim color' },
    { label: 'Border color' },
    { label: 'Bordered' },
]
const UtilitiesSidePanel = () => (
	<SidePanel.ScrollNav list={list} />
);
