import { useRouter } from './Hooks';
import PropTypes from 'prop-types';

const RouteNotFoundProps = {
	render: PropTypes.element.isRequired,
};

export const RouteNotFound = ({ render }) => {
	const {
		state: { isNoMatched },
	} = useRouter();

	return isNoMatched ? render : null;
};

RouteNotFound.propTypes = RouteNotFoundProps;
