import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';

export const Forms = () => {
	const { getSession, setSession } = useSession();
	const title = 'Forms';
	const context = `is used to create interactive controls for web-based forms in order to accept data from the user.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<FormsMain />}
				side={<FormsSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Forms.propTypes = {};

const FormsMain = () => (
	<>
		<Section.Example />
		<Section.Validation />
		<Section.Checkbox />
		<Section.Radio />
		<Section.InlineCheckboxes />
		<Section.Switch />
		<Section.Range />
		<Section.Color />
		<Section.Textarea />
		<Section.Select />
		<Section.ActiveState />
		<Section.DisabledState />
	</>
);

export const FormsSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Forms Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.form}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius', 'border']}
			allowEngraveWithConcave
		/>
	</ul>
);
