import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';

export const Card = () => {
	const { getSession, setSession } = useSession();
	const title = 'Card';
	const context = `it is a container to make you more creative.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<CardMain />}
				side={<CardSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Card.propTypes = {};

const CardMain = () => (
	<>
		<Section.Example />  
		<Section.Thumbnail />
		<Section.Footer />
		<Section.TableInside />
	</>
);

const CardSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Card Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.card}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius', 'border']}
		/>
	</ul>
);
