import React, { useEffect } from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes'; 

export const Example = () => (
    <Section
        title='Example'
        context={<>it is built with flexbox.</>}>
        <HoverCopy code={code.Example}>
            <CodeReadOnly code={code.Example} />
        </HoverCopy>
        <nav onClick={e => e.preventDefault()} className="n-nav">
            <a className="n-nav-link active" href="#">Active</a>
            <a className="n-nav-link" href="#">Link</a>
            <a className="n-nav-link" href="#">Link</a>
            <a className="n-nav-link disabled" href="#">(Disabled)</a>
        </nav>
    </Section>
);

export const FlatShadow = () => (
    <Section
        title='Flat Shadow'
        context={<>by adding <b>.n-nav-flat</b> class it will appear in flat top style.</>}>
        <HoverCopy code={code.FlatShadow}>
            <CodeReadOnly code={code.FlatShadow} />
        </HoverCopy>
        <nav onClick={e => e.preventDefault()} className="n-nav n-nav-flat">
            <a className="n-nav-link active" href="#">Active</a>
            <a className="n-nav-link" href="#">Link</a>
            <a className="n-nav-link" href="#">Link</a>
            <a className="n-nav-link disabled" href="#">(Disabled)</a>
        </nav>
    </Section>
);

