import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { Chaperone } from 'chaperone';
import { MainContainer, SidePanel, HeadGear } from 'Templates';
// import * as Section from './Sections';
// import * as Table from './Tables';

export const Reactrans = () => {
	const { getSession, setSession } = useSession();
	const title = 'Reactrans';
	const context = `is a form validator library that is delightfully made in react. It is so
	light with unique setting and cognitive design. the rules are
	eloquent which will make the schema so powerful yet easy to understand,
	and implement on any kind of components. unlike any other form validator library,
	Reactrans is complex that allows you to bend
	and shape it on almost what ever you want. furthermore, Reactrans is capable on working both controllable and uncontrollable form fields and to know more just read the documentation below.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - My React Libraries`}
					description={context.slice(0, 100)}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<ReactransMain />}
				side={<ReactransSidePanel />}
			/>
		</>
	);
};

Reactrans.propTypes = {};

const ReactransMain = () => (
	<Chaperone
		triggerId='Reactrans'
		onDone={() => console.log('done')}
		onSkip={(e) => console.log('skip')}>
		<h2 id='api'>API</h2>
		<hr />
	</Chaperone>
);

const ReactransSidePanel = () => {
	const list = [
		{ label: 'Installation' },
		{ label: 'Importing' },
		{
			label: 'Getting Started',
			sub: [{ label: 'Controllable Fields' }],
		},
		{
			label: 'API',
			sub: [{ label: 'Rules' }],
		},
	];
	return <SidePanel.ScrollNav list={list} />;
};
