import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { Chaperone } from 'chaperone';
import { MainContainer, SidePanel, Article, HeadGear } from 'Templates';
import * as Section from './Sections';
import * as Table from './Tables';
import { useAnimateGroup } from 'Snippets/Animation';

export const Reatable = () => {
	const { getSession, setSession } = useSession();
	const title = 'Reatable';
	const context = `is a react dynamic table which displays rows of data. It is so
	light with catchy setting and cognitive design structure. reatable is not limited with 
	it's design and it's features since most of it's aspects are cuztomizable so table's creativity 
	is endless within its bootstrap.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - My React Libraries`}
					description={context.slice(0, 100)}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<ReatableMain />}
				side={<ReatableSidePanel />}
			/>
		</>
	);
};

Reatable.propTypes = {};

const ReatableMain = () => (
	<Chaperone
		triggerId='Reatable'
		onDone={() => console.log('done')}
		onSkip={(e) => console.log('skip')}>
		<Section.Installation />
		<Section.Importing />
		<Section.GettingStarted />
		<Section.MutatingTableCell />
		<Section.ColumnGroup />
		<Section.TableWithPagination />
		<Section.SortableTable />
		<Section.CollapsibleTable />
		<Section.CollapsibleTableUsingButton />
		<Section.TableFiltration />
		<Section.AsynchronousTable />
		<Section.CustomizableTableInterfaces />
		<Section.CustomizeLoader />
		<Section.CustomizablePagination />
		<Section.CreateYourOwnPagination />
		<Section.ChangablePageSize />
		<Section.CreateFilterFirstLetters />
		<Section.CreateFilterBySearch />
		<Section.CustomizeTableContentRowAndCell />
		<Section.CustomizeTableContentEmptyData />
		<Section.CustomizeTableContentColumnHeader />
		<Section.PassedRowDataOnModal />
		<Section.TableSelection />

		<Article title='API'>
			<Table.TableAPI />
			<Table.TableContent />
			<Table.Pagination />
			<Table.Loader />
			<Article as='h3' title='Hooks'>
				<Table.usePagination />
				<Table.useSort />
				<Table.useFilter />
				<Table.useColumn />
			</Article>
		</Article>
	</Chaperone>
);

const ReatableSidePanel = () => {
	const list = [
		{ label: 'Installation' },
		{ label: 'Importing' },
		{
			label: 'Getting Started',
			sub: [
				{ label: 'Mutating Table Cell' },
				{ label: 'Column Group' },
				{ label: 'Table With Pagination' },
				{ label: 'Sortable Table' },
				{ label: 'Collapsible Table' },
				{ label: 'Collapsible Table Using Button' },
				{ label: 'Table Filtration' },
				{ label: 'Asynchronous Table' },
			],
		},
		{
			label: 'Customizable Table Interfaces',
			sub: [
				{ label: 'Customize Loader' },
				{ label: 'Customizable Pagination Component' },
				{ label: 'Create Your Own Pagination' },
				{ label: 'Changable Page Size' },
				{ label: 'Create Filter By First Letters' },
				{ label: 'Create Filter By Search' },
				{ label: 'Customize TableContent Row & Cell' },
				{ label: 'Customize TableContent Empty Data' },
				{ label: 'Customize TableContent Column Header' },
				{ label: 'Passing A Row Data On Modal' },
				{ label: 'Table Selection' },
			],
		},
		{
			label: 'API',
			sub: [
				{ label: 'Table' },
				{ label: 'TableContent' },
				{ label: 'Pagination' },
				{ label: 'Loader' },
				{
					label: 'Hooks',
					sub: [
						{ label: 'usePagination' },
						{ label: 'useSort' },
						{ label: 'useFilter' },
						{ label: 'useColumn' },
					],
				},
			],
		},
	];
	return <SidePanel.ScrollNav list={list} />;
};

const Tab = () => {
	const [register, show, toggle] = useAnimateGroup(
		{
			animation: {
				enter: {
					height: 'auto',
					opacity: 1,
				},
				exit: { height: 0, opacity: 0 },
			},
		},
		false
	);
	return (
		<div className='tab'>
			<ul>
				<li>
					<a
						href='#'
						onClick={(e) => {
							e.preventDefault();
							toggle('one');
						}}>
						one
					</a>
				</li>
				<li>
					<a
						href='#'
						onClick={(e) => {
							e.preventDefault();
							toggle('two');
						}}>
						two
					</a>
				</li>
			</ul>
			<div
				className='tab-content'
				ref={register('one', true)}
				style={{ display: show('one') ? 'block' : 'none', height: '400px' }}>
				1
			</div>
			<div
				className='tab-content'
				ref={register('two')}
				style={{ display: show('two') ? 'block' : 'none', height: '400px' }}>
				2
			</div>
		</div>
	);
};
