import React from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
export const Example = () => (
	<Section
		title='Examples'
		context='here is a quick example of the input group style.'>
		<HoverCopy code={code.Example}>
			<CodeReadOnly code={code.Example} />
        </HoverCopy>
        <div className="n-input-group">  
            <input className="n-form-control" placeholder="enter username..." />
            <div className="n-input-group-append">
                <div className="n-input-group-text">@mytabworks.com</div>
            </div>
        </div><br/>
        <div className="n-input-group">  
            <div className="n-input-group-prepend">
                <div className="n-input-group-text">$</div>
            </div>
            <input className="n-form-control" /> 
        </div><br/>
        <div className="n-input-group">  
            <div className="n-input-group-prepend">
                <div className="n-input-group-text">$</div>
            </div>
            <input className="n-form-control" /> 
            <div className="n-input-group-append">
                <div className="n-input-group-text">.00</div>
            </div>
        </div><br/>
        <div className="n-input-group">  
            <div className="n-input-group-prepend">
                <div className="n-input-group-text">textarea</div>
            </div>
            <textarea className="n-form-control" />
        </div>
	</Section>
);

export const CheckboxAndRadioAddOn = () => (
	<Section
		title='Checkbox and radio addons'
		context='you can addon checkboxes and radios.'>
		<HoverCopy code={code.CheckboxAndRadioAddOn}>
			<CodeReadOnly code={code.CheckboxAndRadioAddOn} />
        </HoverCopy>
        <div className="n-input-group">  
            <div className="n-input-group-prepend"> 
                <div className="n-input-group-text">
                    <input className="n-form-control" type="checkbox" />
                </div> 
            </div>
            <input className="n-form-control" /> 
        </div><br/>
        <div className="n-input-group">  
            <div className="n-input-group-prepend"> 
                <div className="n-input-group-text">
                    <input className="n-form-control" type="radio" />
                </div> 
            </div>
            <input className="n-form-control" /> 
        </div>
	</Section>
);

export const ButtonAddOn = () => (
	<Section
		title='Button addons'
		context='you can also addon buttons.'>
		<HoverCopy code={code.ButtonAddOn}>
			<CodeReadOnly code={code.ButtonAddOn} />
        </HoverCopy>
        <div className="n-input-group">  
            <input className="n-form-control" />
            <div className="n-input-group-append">
                <button className="n-btn">Button</button>
            </div>
        </div><br/>
        <div className="n-input-group">  
            <div className="n-input-group-prepend">
                <button className="n-btn">Button</button>
            </div>
            <input className="n-form-control" /> 
        </div><br/>
        <div className="n-input-group">  
            <div className="n-input-group-prepend">
                <button className="n-btn">Button</button>
            </div>
            <input className="n-form-control" /> 
            <div className="n-input-group-append">
                <button className="n-btn">Button</button>
            </div>
        </div>
	</Section>
);

export const MultipleInputs = () => (
	<Section
		title='Multiple inputs'
		context='you can group multiple inputs.'>
		<HoverCopy code={code.MultipleInputs}>
			<CodeReadOnly code={code.MultipleInputs} />
        </HoverCopy>
        <div className="n-input-group">
            <div className="n-input-group-prepend">
                <span className="n-input-group-text">$</span>
            </div>  
            <input className="n-form-control" />
            <input className="n-form-control" placeholder="00" /> 
        </div><br/> 
        <div className="n-input-group">  
            <div className="n-input-group-prepend">
                <span className="n-input-group-text">time</span>
            </div>
            <input className="n-form-control" />
            <div className="n-input-group-prepend">
                <span className="n-input-group-text">:</span>
            </div>
            <input className="n-form-control" /> 
            <div className="n-input-group-append">
                <button className="n-btn">submit</button>
            </div>
        </div>
	</Section>
);

export const MultipleAddons = () => (
	<Section
		title='Multiple addons'
		context='you can add multiple addons.'>
		<HoverCopy code={code.MultipleAddons}>
			<CodeReadOnly code={code.MultipleAddons} />
        </HoverCopy>
        <div className="n-input-group"> 
            <input className="n-form-control" /> 
            <div className="n-input-group-append">
                <span className="n-input-group-text">@mytabworks.com</span>
            </div>
            <div className="n-input-group-append">
                <button className="n-btn">submit</button>
            </div>
        </div>
	</Section>
);