import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session'; 
import { MainContainer, SidePanel, Article, HeadGear } from 'Templates';
import * as Section from './Sections';
import * as Table from './Tables';

export const Chaperone = () => {
	const { getSession, setSession } = useSession();
	const title = 'Chaperone';
	const context = `is a react library which helps and guide the user interacts with UI step by step to have a better user experience in your react components. 
	Chaperone gives you alot of option with such ability to make your user experience alot better. it allow such gesture to tell the user what to do next even without reading the dialog.
	it is even flexible in any component whether it is forwardRef or not.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - My React Libraries`}
					description={context.slice(0, 100)}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<ChaperoneMain />}
				side={<ChaperoneSidePanel />}
			/>
		</>
	);
};

Chaperone.propTypes = {};

const ChaperoneMain = () => (
	<>
		<Section.Installation />
		<Section.Importing />
		<Section.GettingStarted />
		<Section.ChaperoneStepSingleton />
		<Section.ChaperoneMultipleID />
		<Section.ChaperoneWithGestures />
		<Article title='API'>
			<Table.Chaperone />
			<Table.ChaperoneStep />
			<Table.Trigger />
		</Article>
	</>
);

const ChaperoneSidePanel = () => {
	const list = [
		{ label: 'Installation' },
		{ label: 'Imports' },
		{
			label: 'Getting Started',
			sub: [
				{ label: 'ChaperoneStep Singleton' },
				{ label: 'Chaperone Multiple ID' },
				{ label: 'Chaperone With Gestures' },
			],
		},
		{
			label: 'API',
			sub: [
				{ label: 'Chaperone' },
				{ label: 'ChaperoneStep' },
				{ label: 'Trigger' },
			],
		},
	];
	return <SidePanel.ScrollNav list={list} />;
};
