import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';
import * as Table from './Tables';
export const Collapse = () => {
	const { getSession, setSession } = useSession();
	const title = 'Collapse';
	const context = `it is toggable to expand and hide content.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<CollapseMain />}
				side={<CollapseSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Collapse.propTypes = {};

const CollapseMain = () => (
	<>
		<Section.Example /> 
		<Section.ShadowLess />
		<Section.Accordion />
		<Section.Javascript /> 
		<Table.Methods />
		<Table.Events />
		<Section.Events />
	</>
);

const CollapseSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Collapse Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.collapse}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius']}
			allowEngrave
		/>
	</ul>
);
