import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';

export const Navs = () => {
	const { getSession, setSession } = useSession();
	const title = 'Navs';
	const context = `it is a base navigation which is use to navigate.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<NavsMain />}
				side={<NavsSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Navs.propTypes = {};

const NavsMain = () => (
	<>
		<Section.Example />  
        <Section.FlatShadow />
        <Section.NoHover />
        <Section.Column />
	</>
);

const NavsSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Nav Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.navs}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius']}
		/>
	</ul>
);
