import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';

export const Button = () => {
	const { getSession, setSession } = useSession();
	const title = 'Button';
	const context = `it is a neumorphism button which is use for form, actions, etc.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<ButtonMain />}
				side={<ButtonSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Button.propTypes = {};

const ButtonMain = () => (
	<>
		<Section.Example />
		<Section.ButtonTags />
		<Section.ButtonSize />
		<Section.ActiveState />
		<Section.DisabledState />
	</>
);

const ButtonSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Button Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.button}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius']}
			allowConvex
		/>
	</ul>
);
