import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';

export const ProgressBar = () => {
	const { getSession, setSession } = useSession();
	const title = 'Progress Bar';
	const context = `it is a progression bar that use to see the progress.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<ProgressBarMain />}
				side={<ProgressBarSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

ProgressBar.propTypes = {};

const ProgressBarMain = () => (
	<>
		<Section.Example /> 
		<Section.Percentage />
	</>
);

const ProgressBarSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Progress Bar Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.progress}
			settingsAllowed={['distance', 'blur', 'intensity', 'border']}
			allowEngraveWithConcave
		/>
	</ul>
);
