export const CustomizablePagination = `import React from "react";
import { Table, TableContent, Pagination } from "reatable";
import data from "human-data.json";
import {columns, data} from "human-data"; 

const PaginationContainer = ({ children }) => (
    <ul className="n-nav n-nav-flated n-no-hover">
        {children}
    </ul>
)

const PaginationList = ({ children, active, disabled, onClick }) => ( 
    <a 
       className={\`n-nav-link\${active 
            ? " active" 
            : disabled 
            ? " disabled" 
            : ""}\`
       }
       href="#" 
       onClick={onClick}>
        {children}
    </a> 
)

#render(
    <Table  
        columns={columns} 
        data={data} 
        pagination={{
            pageSize: 5
        }}>
        <div className="overflow-x-auto">
            <TableContent className="n-table n-no-shadow sortable-table" />
            <div className="d-flex flex-end-x">
                <Pagination 
                    prevLabel=""
                    nextLabel=""
                    as={{
                        Container: PaginationContainer,
                        List: PaginationList
                    }} 
                />
            </div>
        </div>
    </Table>
) `;

export const CreateYourOwnPagination = `import React from "react";
import { Table, TableContent, usePagination } from "reatable";
import {columns, data} from "human-data"; 

const CustomPagination = () => {
    const { allowPagination, totalPages, page, setPage } = usePagination();
    return allowPagination && (
        <nav className="n-nav">
            {Array.from(new Int32Array(totalPages)).map((v, index) => {
                const pageNumber = index + 1
                return (
                    <a key={index}
                        href="#" 
                        className={\`n-nav-link\${ 
                            pageNumber === page 
                            ? " active"  
                            : ""}\`
                        }
                        onClick={(e) => {
                            e.preventDefault()
                            setPage(pageNumber)
                        }}>
                        {pageNumber}
                    </a>
                )
            })}
        </nav>
    )
}

#render(
    <Table  
        columns={columns} 
        data={data} 
        pagination={{
            pageSize: 10
        }}>
        <div className="overflow-x-auto">  
            <TableContent className="n-table n-no-shadow sortable-table" />
            <div className="d-flex flex-end-x">
                <CustomPagination />
            </div>
        </div>
    </Table>
) `;

export const ChangablePageSize = `import React from "react";
import { Table, TableContent, Pagination } from "reatable";
import {columns, data} from "human-data"; 

const TableWrapper = () => {
    const [pagesize, setPagesize] = React.useState(5)

    const showStyle = {
        borderRadius: "2px",
        padding: "5px 10px",
        marginBottom: "16px"
    }
    const sizeSelectionStyle = {
        ...showStyle, 
        borderColor: '#f0f0f0'
    }

    return (
        <Table  
            columns={columns} 
            data={data} 
            pagination={{
                pageSize: pagesize
            }}>
            <div className="overflow-x-auto">
                <TableContent className="n-table n-no-shadow sortable-table" />
                <div className="d-flex flex-spb-x">
                    <div className="d-flex">
                        <span style={showStyle}>show</span>
                        <select value={pagesize} 
                            onChange={({target}) => setPagesize(parseInt(target.value)) }
                            style={sizeSelectionStyle}>
                            <option value="5">5</option>
                            <option value="10">10</option>
                            <option value="15">15</option>
                            <option value="20">20</option>
                        </select>
                    </div>
                    <Pagination /> 
                </div>
            </div>
        </Table>
    )
}

#render(<TableWrapper />)`;

