import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session'; 
import { MainContainer, SidePanel, Article, HeadGear } from 'Templates';
import * as Section from './Sections';
import * as Table from './Tables';

export const Poppingjs = () => {
	const { getSession, setSession } = useSession();
	const title = 'Poppingjs';
	const context = `is a tooltip & popover positioning engine which is delightfully made in pure vanila javascript without dependency what so ever.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>
			 <HeadGear>
				<HeadGear.Meta 
					title={`Mytabworks | ${title} - Javascript Libraries`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
			</HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<PoppingjsMain />}
				side={<PoppingjsSidePanel />}
			/>
		</>
	);
};

Poppingjs.propTypes = {}; 

const PoppingjsMain = () => (
	<>
		<Section.Installation />
		<Section.Importing />
		<Section.GettingStarted /> 
		<Section.CreatePlugin />
		<Section.Fliping /> 
		<Section.Persistent />
		<Section.Animated />
		<Article title='API'>
			<Table.Popping />
			<Table.Methods />
		</Article>
	</>
);

const PoppingjsSidePanel = () => {
	const list = [
		{ label: 'Installation' },
		{ label: 'Imports' },
		{
			label: 'Getting Started',
			sub: [
				{ label: 'Create Plugin' },
				{ label: 'Flipping' },
				{ label: 'Persistent' },
				{ label: 'Animated' },
			],
		},
		{
			label: 'API',
			sub: [
				{ label: 'Popping' },
				{ label: 'Popping Methods' },
			],
		},
	];
	return <SidePanel.ScrollNav list={list} />;
};
