import React, { useEffect } from 'react';
import Dragger from 'draggerjs'
import { SectionWithCode, Note } from 'Templates';
import * as code from '../StringCodes'; 

export const Droppable = () => {

    useEffect(() => {
        const dragger = new Dragger('#drag-droppable', {
            draggable: '.draggable-box',
            droppable: '.droppable-area',
        })
        dragger.on('dragstart', (e) => {
            const target = e.target
            if(e.isDraggableElement) {
                e.preventDefault() 
                target.classList.add('grabbed')
                target.classList.add('n-float')
                target.classList.remove('n-flat')
            }
           
        })
        dragger.on('dragend', (e) => {
            const target = e.target
            if(e.isDraggableElement) { 
                /*async it to show transition*/
                setTimeout(() => {
                    target.classList.add('n-flat')
                    target.classList.remove('n-float')
                    target.classList.remove('grabbed')
                },0)
            }
        })
        dragger.on('dragenter', (e) => {
            if(e.isDraggableElement && e.target.parentElement !== e.currentDroppable) { 
                e.currentDroppable.classList.add('n-engrave') 
            }
        })
        dragger.on('dragexit', (e) => {
            if(e.isDraggableElement && e.target.parentElement !== e.currentDroppable) { 
                e.currentDroppable.classList.remove('n-engrave') 
            }
        })
        dragger.on('drop', (e) => {
            if(e.isDraggableElement) {
                if(e.target.parentElement !== e.currentDroppable)
                    e.currentDroppable.appendChild(e.target) 
                e.currentDroppable.classList.remove('n-engrave') 
            }
        })
        return () => dragger.destroy()
    }, [])

    return (
        <>  
            <SectionWithCode
                readonly
                title='Droppable'
                context='you can use droppable as well.'
                code={code.Droppable}
            >
                <div id="drag-droppable" className="drag-container" data-example>  
                    <div className="droppable-area n-no-shadow n-border-color">
                        <div className="draggable-box n-no-shadow n-flat">
                            <h1>1</h1>
                        </div>
                        <div className="draggable-box n-no-shadow n-flat">
                            <h1>2</h1>
                        </div>
                    </div>
                    <div className="droppable-area n-no-shadow n-border-color"></div>
                </div>
                <Note
                    variant='warning'
                    title='remember'
                    context={<>When using <b>droppable</b> make sure to add <b>[hidden] {'{'} display: none !important; {'}'}</b> in your stylesheet. due to some changes in browsers which <b>hidden</b> property gets overridden.</>}
                    style={{ marginTop: '20px' }}
                />
            </SectionWithCode>
        </>
    );
}