import React from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
const IMG = ({className}) => (
    <svg className={className} width="100%" height="180" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: Image cap">
        <rect width="100%" height="100%" fill="#868e96"></rect>
        <text x="50%" y="50%" fill="#dee2e6" dy=".3em">Image</text>
    </svg>
)
const lorem = "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."
export const Example = () => (
	<Section
		title='Example'
		context={<>the size is depend on the <b>width</b> you apply or the width of wrapper.</>}>
		<HoverCopy code={code.Example}>
			<CodeReadOnly code={code.Example} />
        </HoverCopy>
        <div className="n-card">
            <div className="n-card-header">
                Card Header
            </div>
            <div className="n-card-body">
                <p className="n-card-text">{lorem}</p>
            </div>
        </div>
	</Section>
);

export const Thumbnail = () => (
	<Section
		title='Thumbnail'
        context={<>you can use it as thumbnail.</>}>
		<HoverCopy code={code.Thumbnail}>
			<CodeReadOnly code={code.Thumbnail} />
        </HoverCopy>
        <div className="n-card">
            <IMG className="n-card-img" alt="image" src="/image.png" />
            <div className="n-card-body">
                <p className="n-card-text">{lorem}</p>
            </div> 
        </div>
	</Section>
);

export const Footer = () => (
	<Section
		title='Footer'
		context='card has footer for you.'>
		<HoverCopy code={code.Footer}>
			<CodeReadOnly code={code.Footer} />
        </HoverCopy>
        <div className="n-card">
            <div className="n-card-header">
                Card Header
            </div>
            <div className="n-card-body">
                <p className="n-card-text">{lorem}</p>
            </div>
            <div className="n-card-footer">
                Card Footer
            </div>
        </div>
	</Section>
);

export const TableInside = () => (
	<Section
		title='Card Table'
		context='you can have table inside.'>
		<HoverCopy code={code.TableInside}>
			<CodeReadOnly code={code.TableInside} />
        </HoverCopy>
        <div className="n-card">
            <div className="n-card-header">
                Card Header
            </div>
            <div className="n-card-body">
                <table className="n-table">
                    <thead>
                        <tr><th>Name</th><th>Age</th><th>Gender</th></tr>
                    </thead>
                    <tbody>
                        <tr><td>Eugene</td><td>20</td><td>male</td></tr>
                        <tr><td>Choi</td><td>30</td><td>male</td></tr>
                        <tr><td>Man</td><td>10</td><td>female</td></tr>
                        <tr><td>Boyna</td><td>10</td><td>male</td></tr>
                    </tbody>
                </table> 
            </div>
        </div>
	</Section>
);