import React from 'react';
import { SectionWithCode, Note } from 'Templates';
import * as chaperone from 'chaperone';
import * as code from './StringCodes';

export const Installation = () => (
	<SectionWithCode
		readonly
		title='Installation'
		code={`npm i chaperone`}
		language='js'
		theme={{
			plain: {
				color: 'rgb(215,215,215)',
				backgroundColor: 'rgb(86,86,86)',
			},
			styles: [],
		}}
	/>
);

export const Importing = () => (
	<SectionWithCode readonly title='Imports' code={code.Importing} />
);

export const GettingStarted = () => (
	<>
		<SectionWithCode
			title='Getting Started'
			context='it is the basic usage of chaperone.'
			scope={{ ...chaperone }}
			code={code.GettingStarted}
		/>
		<Note
			context={
				<>
					Don't forget to assign <b>isDone</b> at the last step or else it will
					throw exception.
				</>
			}
			style={{ marginTop: '-20px', marginBottom: '20px' }}
		/>
	</>
);

export const ChaperoneStepSingleton = () => (
	<SectionWithCode
		title='ChaperoneStep Singleton'
		context={
			<>
				it is use to make multiple and different id steps in single{' '}
				<b>ChaperoneStep</b>.
			</>
		}
		scope={{ ...chaperone }}
		code={code.ChaperoneStepSingleton}
	/>
);

export const ChaperoneMultipleID = () => (
	<>
		<SectionWithCode
			title='Multiple ID and Chaining'
			context={
				<>
					you can create multiple guide in single <b>Chaperone</b> and you can
					chain another ID after done or skip.
				</>
			}
			scope={{ ...chaperone }}
			code={code.ChaperoneMultipleID}
		/>
		<Note
			variant='danger'
			context={
				<>
					you must be extra careful when using <b>onDone</b> and <b>onSkip</b>{' '}
					in <b>Chaperone</b> component. especially if the component is having
					some sort of state. or else it will be called infinitely and will
					cause error in your app. as much as posible use hook{' '}
					<b>useCallback</b> without array dependency to prevent some sort of
					error any further.
				</>
			}
			style={{ marginTop: '-20px', marginBottom: '20px' }}
		/>
	</>
);

export const ChaperoneWithGestures = () => (
	<SectionWithCode
		title='Chaperone With Gestures'
		context={<>you can create and append gestures.</>}
		scope={{ ...chaperone }}
		code={code.ChaperoneWithGestures}
	/>
);
