export class AbortController {
	signal = { aborted: false };
	abort() {
		clearTimeout(this.signal.aborted);
	}
}
export const fetch = (url, { signal = {}, body }) => {
	const { sortOrderBy, sortOrder, currentPage, pageSize, filters = [] } = body;
	let source = [...data];
	if (filters.length) {
		source = filters.reduce((result, { dataIndex, value }) => {
			const transformed = value.map(
				(pattern) => new RegExp(`^${pattern.replace(/%/g, '.*')}$`, 'i')
			);
			return result.filter((row) =>
				transformed.some((regexp) => regexp.test(row[dataIndex]))
			);
		}, source);
	}
	const rowsCount = source.length;
	if (sortOrderBy) {
		const isDesc = sortOrder === 'desc';
		const reverse = isDesc ? -1 : 1;
		source = source.sort((a, b) => {
			const isNumber =
				typeof a[sortOrderBy] === 'number' || /^\d+$/.test(a[sortOrderBy]);
			const result = isNumber
				? a[sortOrderBy] - b[sortOrderBy]
				: a[sortOrderBy] < b[sortOrderBy]
				? -1
				: a[sortOrderBy] > b[sortOrderBy]
				? 1
				: 0;
			return result * reverse;
		});
	}

	if (pageSize && pageSize < data.length)
		source = source.slice(
			currentPage * pageSize - pageSize,
			currentPage * pageSize
		);
	return new Promise((resolve, reject) => {
		signal.aborted = setTimeout(
			() =>
				resolve(
					new Response(
						new Blob(
							[
								JSON.stringify(
									{
										data: source,
										rowsCount,
									},
									null,
									2
								),
							],
							{ type: 'application/json' }
						),
						{ status: 200 }
					)
				),
			Math.round(2000 * Math.random()) + 1000
		);
	});
};

