import React, { useState } from 'react';
import { useParams, Link } from 'reaouter';
import request from 'requestjs';
import { Toast } from 'Snippets/Toast';
import { Spinner } from 'Snippets/Spinner';
import { uid, cookie } from 'Common/Utils';
import { GoogleAppURL } from 'Common/API';

export const Unsubscribe = () => {
	const [done, setDone] = useState(false);
	const { email } = useParams();

	const handleSubmit = (event) => {
		event.preventDefault();
		const cookieKey = 'USBC';
		const unsubscribed = cookie.get(cookieKey);
		if (unsubscribed) {
			return Toast.create(
				<>
					you have already unsubscribed <b>{atob(unsubscribed)}</b>. try again
					tomorrow
				</>
			);
		}

		const key = uid();
		Toast.create(
			<div className='d-flex flex-center-y'>
				<Spinner loading={true} />
				unsubscribing...
			</div>,
			{ key, options: { duration: 20000 } }
		);

		request(GoogleAppURL, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/x-www-form-urlencoded',
			},
			responseType: 'json',
			body: `subscriber=${email}&action=unsubscribe`,
		})
		.then((response) => {
			if (response.status >= 400) {
				throw Error('');
			}
		})
		.then(() => {
			Toast.create(
				<div className='d-flex flex-center-y'>
					<Spinner success={true} />
					We will no longer notify you on anything related mytabworks
				</div>,
				{ key, options: { duration: 4000 } }
			);
			setDone(true);
		})
		.catch(() =>
			Toast.create(
				<div className='d-flex flex-center-y'>
					<Spinner success={false} />
					something went wrong try again later
				</div>,
				{ key, options: { duration: 4000 } }
			)
		);
		cookie.set(cookieKey, btoa(email), 1);
	};

	return (
		<div className='container'>
			<div className='row'>
				<div className='col-sm-8 mx-auto'>
					<div className='n-flat p-5 my-5 br-5 t-center'>
						{done ? (
							<>
								<h2 className='my-3'>
									<Spinner success='true' /> Successfully unsubscribe
								</h2>
								<p className='n-text-dim-color mb-3'>
									you have successfully unsubscribe <b>{email}</b> on{' '}
									<b>mytabworks</b><br/>
									We will no longer notify you on anything related{' '}
									<b>mytabworks</b>.<br/> but you can still subscribe anytime
								    if you ever changed your mind.
								</p>
								<Link
									className='n-btn n-btn-lg d-block w-full mb-3'
									to='/'
									label='Return Home'
								/>
							</>
						) : (
							<>
								<h2 className='my-3'>Do you really want to unsubscribe?</h2>
								<p className='n-text-dim-color mb-3'>
									you are currently subscribed to <b>mytabworks</b> as
									<br /> <b>{email}</b>
								</p>
								<button
									className='n-btn n-btn-lg d-block w-full mb-3'
									type='button'
									onClick={handleSubmit}>
									Proceed
								</button>
							</>
						)}
					</div>
				</div>
			</div>
		</div>
	);
};
