import React, { useEffect } from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes'; 

export const Example = () => (
    <Section
        title='Example'
        context={<>progress bar examples with variant of <b>success</b>, <b>warning</b>, and <b>danger</b>.</>}>
        <HoverCopy code={code.Example}>
            <CodeReadOnly code={code.Example} />
        </HoverCopy>
        <h6>default</h6>
        <div className="n-progress">
            <div className="n-progress-bar" style={{width: '100%'}} />
        </div>
        <br/>
        <h6>success</h6>
        <div className="n-progress">
            <div className="n-progress-bar n-success" style={{width: '60%'}} />
        </div>
        <br/>
        <h6>warning</h6>
        <div className="n-progress">
            <div className="n-progress-bar n-warning" style={{width: '30%'}} />
        </div>
        <br/>
        <h6>danger</h6>
        <div className="n-progress">
            <div className="n-progress-bar n-danger" style={{width: '90%'}} />
        </div>
    </Section>
);

export const Percentage = () => (
    <Section
        title='Progress bar with percentage'
        context={<>you can also show the percentage by adding a attribute <b>data-percentage="60%"</b> on <b>.n-progress-bar</b> class.</>}>
        <HoverCopy code={code.Percentage}>
            <CodeReadOnly code={code.Percentage} />
        </HoverCopy>
        <br/>
        <h6>default</h6>
        <div className="n-progress">
            <div className="n-progress-bar" data-percentage="100%" style={{width: '100%'}} />
        </div>
        <br/>
        <br/>
        <h6>success</h6>
        <div className="n-progress">
            <div className="n-progress-bar n-success" data-percentage="60%" style={{width: '60%'}} />
        </div>
        <br/>
        <br/>
        <h6>warning</h6>
        <div className="n-progress">
            <div className="n-progress-bar n-warning" data-percentage="30%" style={{width: '30%'}} />
        </div>
        <br/>
        <br/>
        <h6>danger</h6>
        <div className="n-progress">
            <div className="n-progress-bar n-danger" data-percentage="90%" style={{width: '90%'}} />
        </div>
    </Section>
);