import React from 'react'
import { NavBar, Home, AboutMe, Practice, Skills, Contact, Footer } from './Sections'
import { useScroll } from 'Snippets/Animation'
import { HeadGear } from 'Templates'
import { animated } from 'Common/Utils'
import './index.css'
export const HomePage = () => { 
    useScroll('[data-scroll], [data-src], [data-number]', (target) => {
        if(target.nodeName === "IMG") {
            target.setAttribute('src', target.getAttribute('data-src'))
            target.removeAttribute('data-src')
        } else if (target.hasAttribute('data-number')) {
            const number = parseInt(target.textContent)
            animated(1000, (percent) => {
                target.textContent = Math.round(Math.pow(percent, 4) * number)
            })
            target.removeAttribute('data-number')
        } else {
            target.classList.remove('hidden')
            target.removeAttribute('data-scroll')
        }
    }, 99)
    
	return (
		<>  
            <HeadGear>
                <title>Mytabworks | Fernando Tabamo Jr.</title>
                <meta
                    name="description"
                    content="I'm a Web Developer from Tarlac, Philippines. I enjoy taking challenging and complex problems, and i"
                />
            </HeadGear>
            <NavBar />
            <Home />
            <AboutMe />
            <Practice />
            <Skills /> 
            <Contact />
            <Footer />
		</>
	);
};