import React from 'react';
import { Section, Table } from 'Templates';

const filterData = {
	name: 'like',
	type: 'string',
	default: '-',
	description: (
		<>
			It is the filteration pattern. The pattern is similar to <b>MYSQL</b>{' '}
			<b>LIKE</b>.
			<br />
			<b>%</b> - The percent sign represents zero, one, or multiple characters.
			<br />
			<b>"a%"</b> - Finds any values that start with "a".
			<br />
			<b>"%a"</b> - Finds any values that ends with "a".
			<br />
			<b>"%or%"</b> - Finds any values that have "or" in any position.
			<br />
			<b>"a%o"</b> - Finds any values that start with "a" and ends with "o".
			<br />
			<b>"aloha"</b> - Without <b>%</b> will be recognize as exact.
			<br />
			Although the "%" is only effective in <b>Table</b> when{' '}
			<b>asynchronous</b> is <b>false</b>, but still any pattern entry will be
			passed on <b>onChange</b> event property filters. which will be useful in{' '}
			<b>asynchronous</b> that will be requested in server.
		</>
	),
};

