import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session'; 
import { MainContainer, SidePanel, Article } from 'Templates';
import * as Section from './Sections';
import * as Table from './Tables';

export const Examplejs = () => {
	const { getSession, setSession } = useSession();
	const title = 'Examplejs';
	const context = `is a pure vanila javascript without dependency which is use to drag and drop elements in easy and understabdable way.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<MainContainer
			isLoading={!getSession(title)}
			title={title}
			context={context}
			main={<ExamplejsMain />}
			side={<ExamplejsSidePanel />}
		/>
	);
};

Examplejs.propTypes = {}; 

const ExamplejsMain = () => (
	<>
		<Section.Installation />
		<Section.Importing />
		<Section.GettingStarted /> 
		<Article title='API'>
			<Table.Example />  
		</Article>
	</>
);

const ExamplejsSidePanel = () => {
	const list = [
		{ label: 'Installation' },
		{ label: 'Imports' },
		{
			label: 'Getting Started',
			sub: [

			],
		},
		{
			label: 'API',
			sub: [
				{ label: 'Example' },
			],
		},
	];
	return <SidePanel.ScrollNav list={list} />;
};
