import React, { useEffect } from 'react';
import { useSession } from 'Source/App/Storage/Session';
import { MainContainer, HeadGear } from 'Templates';
import { FormsSidePanel } from '../Forms'; 
import * as Section from './Sections';

export const InputGroup = () => {
	const { getSession, setSession } = useSession();
	const title = 'Input Group';
	const context = ` is a wrapper to enhance an input by adding an icon, text, button or a additional input in front or behind the input field.`;
	useEffect(() => {
		if (!getSession(title)) {
			setSession(title, true);
		}
	}, [title, getSession, setSession]);

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`Mytabworks | ${title} - Neumorphism Project`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer
				isLoading={!getSession(title)}
				title={title}
				context={context}
				main={<InputGroupMain />}
				side={<FormsSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

InputGroup.propTypes = {};

const InputGroupMain = () => (
	<>
		<Section.Example />
		<Section.ButtonAddOn />
		<Section.CheckboxAndRadioAddOn />
		<Section.MultipleInputs />
		<Section.MultipleAddons />
	</>
);
